<template>
  <div class="block">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>部门信息</el-breadcrumb-item>
      <el-breadcrumb-item>部门信息列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!-- 搜索 -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-row :gutter="5">
            <el-col :span="8">
              <el-input
                placeholder="请输入部门名称"
                v-model="DeptInfoVo.deptName"
                clearable
              >
              </el-input>
            </el-col>
            <el-col :span="8">
              <el-select
                v-model="DeptInfoVo.deptFunction"
                placeholder="请选择部门职能"
              >
                <el-option label="教学院系" value="教学院系"></el-option>
                <el-option label="科研机构" value="科研机构"></el-option>
                <el-option
                  label="直属附属医院"
                  value="直属附属医院"
                ></el-option>
                <el-option label="其他" value="其他"></el-option>
              </el-select>
            </el-col>
            <el-col :span="8">
              <el-select
                v-model="DeptInfoVo.deptStatus"
                placeholder="请选择状态"
              >
                <el-option label="运行" value="运行"></el-option>
                <el-option label="停止" value="停止"></el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="10">
          <el-button type="primary" @click="queryPage">搜索</el-button>
          <el-button
            type="danger"
            @click="
              ;(DeptInfoVo.deptStatus = ''),
                (DeptInfoVo.deptFunction = ''),
                (DeptInfoVo.deptName = '')
            "
            >清空</el-button
          >
          <el-button
            type="info"
            @click="
              ;(dialogAddDeptInfo = true),
                (DeptInfoInfo = {}),
                (dialogBewrite = '添加部门信息')
            "
            >添加</el-button
          >
        </el-col>
      </el-row>

      <el-table :data="DeptInfoList" border stripe>
        <el-table-column align="center" type="selection" width="50" />
        <el-table-column
          align="center"
          prop="deptId"
          label="部门编号"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="deptName"
          label="部门名称"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="deptFunction"
          label="部门职能"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="deptEstablishTime"
          label="成立时间"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="deptStatus"
          label="部门状态"
        ></el-table-column>
        <el-table-column
          align="center"
          prop="deptDemo"
          label="部门简介"
        ></el-table-column>
        <el-table-column align="center" label="操作" width="120px">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              v-if="is_authority.is_user_upt"
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editDeptInfo(scope.row)"
              ></el-button>
            </el-tooltip>
            <!-- 删除 -->
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"
              v-if="is_authority.is_user_del"
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page.current"
        :page-sizes="[5, 10, 20, 30, 40]"
        :page-size="page.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
      ></el-pagination>

      <el-dialog
        :title="dialogBewrite"
        :visible.sync="dialogAddDeptInfo"
        width="50%"
        @close="closeInit"
      >
        <el-form
          :model="DeptInfoInfo"
          ref="addDeptInfoRef"
          label-width="70px"
          class="demo-ruleForm"
        >
          <el-form-item label="部门编号">
            <el-input v-model="DeptInfoInfo.deptId"></el-input>
          </el-form-item>
          <el-form-item label="部门名称">
            <el-input v-model="DeptInfoInfo.deptName"></el-input>
          </el-form-item>
          <el-form-item label="部门职能">
            <template>
              <el-select
                v-model="DeptInfoInfo.deptFunction"
                placeholder="请选择"
              >
                <el-option label="教学院系" value="教学院系"></el-option>
                <el-option label="科研机构" value="科研机构"></el-option>
                <el-option
                  label="直属附属医院"
                  value="直属附属医院"
                ></el-option>
                <el-option label="其他" value="其他"></el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item label="成立时间">
            <el-input v-model="DeptInfoInfo.deptEstablishTime"></el-input>
          </el-form-item>
          <el-form-item label="部门状态">
            <template>
              <el-select v-model="DeptInfoInfo.deptStatus" placeholder="请选择">
                <el-option label="运行" value="运行"></el-option>
                <el-option label="停止" value="停止"></el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item label="部门简介">
            <el-input v-model="DeptInfoInfo.deptDemo"></el-input>
          </el-form-item>
          <el-form-item>
            <template>
              <el-button type="primary" @click="addDeptInfo()">保存</el-button>
              <el-button @click="dialogAddDeptInfo = false">取消</el-button>
            </template>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      DeptInfoList: [],
      page: {
        current: 1,
        size: 10,
        total: 0
      },
      DeptInfoVo: {
        page: {
          current: 1,
          size: 5
        }
      },
      dialogAddDeptInfo: false,
      DeptInfoInfo: {},
      dialogBewrite: ''
    }
  },
  created() {
    this.isAuthority()
    this.queryPage()
  },
  computed: {
    ...mapState(['btus'])
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    // 监听每页展示的条数
    handleSizeChange(newSize) {
      this.DeptInfoVo.page.size = newSize
      this.queryPage()
    },
    //监听页码
    handleCurrentChange(newCurrent) {
      this.DeptInfoVo.page.current = newCurrent
      this.queryPage()
    },
    //列表
    async queryPage() {
      const { data: res } = await this.$http.post(
        '/deptInfo/queryDeptInfoList',
        this.DeptInfoVo
      )

      if (res.code != 20000) return this.$message.error(res.message)
      this.DeptInfoList = res.data.dataList.records
      this.page.total = res.data.dataList.total
      this.page.current = res.data.dataList.current
      this.page.size = res.data.dataList.size
    },
    //查询详情
    async editDeptInfo(row) {
      this.dialogAddDeptInfo = true
      this.dialogBewrite = '编辑部门信息'
      const id = row.id

      const { data: res } = await this.$http.get(
        '/deptInfo/getByIdDeptInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.DeptInfoInfo = res.data.entity
    },
    closeInit() {
      this.addDeptInfoForm = {}
      this.dialogAddDeptInfo = false
    },
    addDeptInfo() {
      this.$refs.addDeptInfoRef.validate(async valid => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.post(
          '/deptInfo/saveOrUpdateDeptInfo/',
          this.DeptInfoInfo
        )
        if (res.code != 20000) return this.$message.error(res.message)
        this.dialogAddDeptInfo = false
        this.queryPage()
        return this.$message.success(res.message)
      })
    },
    async del(status) {
      this.$confirm('确认永久删除吗？')
        .then(async _ => {
          const { data: res } = await this.$http.delete(
            '/deptInfo/removeByIdDeptInfo/' + status.id
          )
          if (res.code != 20000) return this.$message.error(res.message)
          this.queryPage()
          return this.$message.success(res.message)
        })
        .catch(_ => {
          return this.$message.info('取消删除')
        })
    }
  }
}
</script>
<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  font-size: 12px;
}

.el-pagination {
  margin-top: 15px;
}
</style>
